import React from 'react';

export default function Features({ featuresArray }){
  return (
    <section id="features" className="session-white text-center container-fluid my-5">
      <div className="row m-md-0 p-md-5">
        {featuresArray.map((feature, index) => (
          <div key={index} className="col-sm-12 col-lg-4 px-4">
            <i className={`fa-solid ${feature.icon} features__icon fa-4x m-5`}></i>
            <h3 className="font-weight-bold">{feature.title}</h3>
            <p>{feature.text}</p>
          </div>
        ))}
      </div>
    </section>
  );
};


